/* ================================= 
  Base Element Styles
==================================== */

* {
  box-sizing: border-box;
}
/* 
    ---Fluid Typography---
    html {
    font-size: calc([minimum size] + ([maximum size] - [minimum size]) * ((100vw - [minimum viewport width]) / ([maximum viewport width] - [minimum viewport width])));
    } 
*/
html {
  /* font-size: 100%; */
  font-size: clamp(100%, 3vw, 18px);
  overflow-x: hidden;
}

body {
  font-family: "Crimson Text", serif;
  line-height: 1.6;
  margin: 0;
  color: #090201;
  position: relative;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
p {
  margin: 0;
}
a {
  text-decoration: none;
  color: #090201;
  display: inline-block;
  padding: 0.5rem;
  transition: 0.3s;
}
a:hover {
  color: #899aab;
}
img {
  max-width: 100%;
}
button {
  border-width: 0;
}

/* ================================= 
  Base Layout Styles
==================================== */

/* ---- Navigation ---- */
.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #eee;
  padding: 0 1.5rem;
}
.main-nav-display {
  display: none;
}
.main-nav {
  display: flex;
}

.menu {
  width: 50%;
  background-color: #fff;
  position: absolute;
  right: -50%;
  top: 0;
  bottom: 0;
  box-shadow: -3px 0 15px rgba(0, 0, 0, 0.2);
  transition: right 0.7s ease-in-out 0.1s;
  /* display: none; */
}
.menu-clear {
  position: absolute;
  top: 0;
  right: 0;
  padding: 1rem;
}
.menu-item {
  padding: 1rem;
  margin-bottom: 1rem;
}
.menu-item:hover {
  background-color: #eee;
}

/* ---- Layout Containers ---- */
.banner {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ccddee;
  padding: 5rem 2rem;
}
.banner:hover {
  cursor: pointer;
}
.banner h1 {
  text-align: center;
}

.main-content {
  padding: 0 1rem 2rem;
}
.hidden {
  display: none;
}
.show {
  display: block;
}
.row {
  /* outline: 1px solid red; */
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  /* 
        上述： 
        改寫在btn(item)自己center
        不然會導致.wrap不再strech
    */
  margin: 0 auto;
  max-width: 1200px;
}
.wrap {
  display: flex;
  flex-wrap: wrap;
}

/* ---- Page Elements ---- */
.row h2 {
  text-align: center;
  padding: 1rem 0;
}

.content-box {
  background-color: #899aab;
  height: 8rem;
  text-align: center;
  padding: 0.7rem;
  flex-grow: 1; /* 均分 */
  min-width: 100%; /* 一欄 */
  margin-bottom: 1.3rem;
}

.btn {
  margin: 0 auto 1.3rem;
  background-color: #ccddee;
  align-self: center;
  padding: 0.5rem 1rem;
  /* 
        上述： 
        .row為container，btn(item)自己center
        不然會導致其他item（如.wrap）跟著center
        不再strech
    */
  transition: 0.4s;
}
.btn:hover {
  color: white;
  background-color: #899aab;
}

/* ================================= 
  Media Queries
==================================== */
/*** mobile first approach ***/

/* 500 - 800px */
@media screen and (min-width: 500px) {
  /* ---- Layout Containers ---- */
  .content-box {
    min-width: 45%; /* 兩欄 */
  }
  .content-box:nth-child(odd) {
    margin-right: 1rem;
  }
  /* ---- Page Elements ---- */
  .main-nav li a {
    padding: 0.8rem 1.7rem; /* 增加點擊範圍＋nav上下稍微加寬 */
  }
}

/* 800 - 1200px */
@media screen and (min-width: 800px) {
  /* ---- Layout Containers ---- */
  .main-nav-display {
    display: block;
  }
  .main-nav-ham {
    display: none;
  }
}

